<template>
  <div :class="{'reset-password-container': isPc, 'reset-password-container-mb': !isPc}">
    <div v-if="!isSuccess" class="reset-password-content">
      <p class="title">修改密碼</p>
      <a-form id="components-form-demo-normal-login" :form="form" class="login-form" @submit="handleSubmit">
        <a-form-item>
          <i class="input-icon" :class="{
        'password-icon-normal': password1State === 1,
        'password-icon-click': password1State === 2,
        'password-icon-error': password1State === 3}"></i>
          <a-popover placement="rightTop" v-if="isPc">
            <template slot="content">
              <div class="reset-password-conditions">
                <p class="title">選擇一個強密碼</p>
                <p :class="{'passw-check-normal': condition1 === 1,
                            'passw-check-pass': condition1 === 2,
                            'passw-check-error': condition1 === 3}">包含至少1個小寫字母；</p>
                <p :class="{'passw-check-normal': condition2 === 1,
                            'passw-check-pass': condition2 === 2,
                            'passw-check-error': condition2 === 3}">包含至少1個大寫字母；</p>
                <p :class="{'passw-check-normal': condition3 === 1,
                            'passw-check-pass': condition3 === 2,
                            'passw-check-error': condition3 === 3}">包含至少1個數字；</p>
                <p :class="{'passw-check-normal': condition4 === 1,
                            'passw-check-pass': condition4 === 2,
                            'passw-check-error': condition4 === 3}">長度範圍為8到18個字符。</p>
              </div>
            </template>
            <a-input v-decorator="['password1',{ rules: [{ required: true, message: '包含至少1個小寫字母' }] }]"
                     :type="password1Type"
                     placeholder="新密碼" @focus="password1State = 2;showError = 0;checkPassword(passwordText1)"
                     @blur="password1State = 1; passwordInit1 = true" @change="onChange1">
            </a-input>
            <i class="input-icon" :class="{'password-show': !password1Show, 'password-hide': password1Show}"
               @click="showPassword1"></i>
          </a-popover>

          <a-input v-if="!isPc" v-decorator="['password1',{ rules: [{ required: true, message: '包含至少1個小寫字母' }] }]"
                   :type="password1Type"
                   placeholder="新密碼" @focus="password1State = 2;showError = 0;checkPassword(passwordText1)"
                   @blur="password1State = 1; passwordInit1 = true" @change="onChange1">
          </a-input>
          <i v-if="!isPc" class="input-icon" :class="{'password-show': !password1Show, 'password-hide': password1Show}"
             @click="showPassword1"></i>

        </a-form-item>
        <a-form-item>
          <i class="input-icon" :class="{
        'password-icon-normal': password2State === 1,
        'password-icon-click': password2State === 2,
        'password-icon-error': password2State === 3}"></i>

          <a-popover placement="rightTop" v-if="isPc">
            <template slot="content">
              <div class="reset-password-conditions">
                <p :style="{'margin-bottom': 0}" :class="{'passw-check-normal': condition5 === 1,
                            'passw-check-pass': condition5 === 2,
                            'passw-check-error': condition5 === 3}">重複上一個密碼。</p>
              </div>
            </template>
            <a-input v-decorator="['password2',{ rules: [{ required: true, message: '重複上一個密碼' }] }]"
                     :type="password2Type" placeholder="確認密碼"
                     @focus="password2State = 2;showError = 0;checkPassword2(passwordText2)"
                     @blur="password2State = 1; passwordInit2 = true" @change="onChange2">
            </a-input>
            <i class="input-icon" :class="{'password-show': !password2Show, 'password-hide': password2Show}"
               @click="showPassword2"></i>
          </a-popover>
          <a-input v-if="!isPc" v-decorator="['password2',{ rules: [{ required: true, message: '重複上一個密碼' }] }]"
                   :type="password2Type" placeholder="確認密碼"
                   @focus="password2State = 2;showError = 0;checkPassword2(passwordText2)"
                   @blur="password2State = 1; passwordInit2 = true" @change="onChange2">
          </a-input>
          <i v-if="!isPc" class="input-icon" :class="{'password-show': !password2Show, 'password-hide': password2Show}"
             @click="showPassword2"></i>

        </a-form-item>
        <a-form-item>
          <a-button type="primary" html-type="submit" :loading="iconLoading" class="login-form-button">重置</a-button>
          <p class="error-mss" :style="{'opacity':showError}">{{errorMss}}</p>
        </a-form-item>
      </a-form>
    </div>
    <div v-if="isSuccess" class="reset-password-success">
      <i class="success-icon" :style="{backgroundImage: 'url(' + $assetsUrl + 'pay/success.svg)'}"></i>
      <p class="title">密碼修改成功</p>
      <p class="description">您的密碼已修改</p>
      <a-button type="primary" html-type="submit" :loading="iconLoading" class="forget-password-form-button"
                @click="goSignin">登入
      </a-button>
    </div>
  </div>

</template>

<script>

  export default {
    beforeCreate() {
      this.form = this.$form.createForm(this);
    },
    data() {
      return {
        password1State: 1,
        password2State: 1,
        password1Show: false,
        password2Show: false,
        password1Type: 'password',
        password2Type: 'password',
        errorMss: '',
        showError: 0,
        iconLoading: false,
        condition1: 1,
        condition2: 1,
        condition3: 1,
        condition4: 1,
        condition5: 1,
        passwordText1: '',
        passwordText2: '',
        passwordInit1: false,
        passwordInit2: false,
        isSuccess: false,
        isPc: this.$isPc()
      };
    },
    methods: {
      handleSubmit(e) {
        e.preventDefault();
        if (this.condition1 !== 2 || this.condition2 !== 2 || this.condition3 !== 2 || this.condition4 !== 2 || this.condition5 !== 2) {
          this.showError = 1
          if (this.condition1 !== 2) {
            this.errorMss = '包含至少1個小寫字母'
          } else if (this.condition2 !== 2) {
            this.errorMss = '包含至少1個大寫字母'
          } else if (this.condition3 !== 2) {
            this.errorMss = '包含至少1個數字'
          } else if (this.condition4 !== 2) {
            this.errorMss = '長度範圍為8到18個字符'
          } else if (this.condition5 !== 2) {
            this.errorMss = '重複上一個密碼'
          }
        } else {
          this.form.validateFields((err, values) => {
            if (!err) {
              let param = {
                email: localStorage.getItem('email'),
                password: values['password1'],
              }
              this.iconLoading = true;
              const that = this
              this.$request.postChangePassword(param, function (data) {
                that.iconLoading = false;
                if (data.status === 201 || data.status === 200) {
                  that.iconLoading = false;
                  that.isSuccess = true
                  localStorage.removeItem('token')
                } else {
                  that.showError = 1;
                  if (data.message.indexOf('Email or Password Invalid') > -1) {
                    that.errorMss = '電子郵件或密碼無效';
                  } else if (data.message.indexOf('We do not recognize this code') > -1) {
                    that.errorMss = '我們無法識別此代碼，請重新獲取驗證碼郵件';
                  } else {
                    that.errorMss = '存在異常，請稍後重試';
                  }
                }
              });
            }
          });
        }

      },
      showPassword2() {
        this.password2Show = !this.password2Show;
        this.password2Type = this.password2Show ? 'text' : 'password';
      },
      showPassword1() {
        this.password1Show = !this.password1Show;
        this.password1Type = this.password1Show ? 'text' : 'password';
      },
      onChange1(e) {
        this.checkPassword(e.target.value);
        this.passwordText1 = e.target.value;
      },
      onChange2(e) {
        this.checkPassword2(e.target.value);
        this.passwordText2 = e.target.value;
      },
      checkPassword(password) {
        this.condition1 = /[a-z]/.test(password) ? 2 : this.passwordInit1 ? 3 : 1;
        this.condition2 = /[A-Z]/.test(password) ? 2 : this.passwordInit1 ? 3 : 1;
        this.condition3 = /[0-9]/.test(password) ? 2 : this.passwordInit1 ? 3 : 1;
        this.condition4 = (password.length >= 8 && password.length <= 18) ? 2 : this.passwordInit1 ? 3 : 1;
      },
      checkPassword2(password) {
        this.condition5 = this.passwordText1 === password ? 2 : this.passwordInit2 ? 3 : 1;
      },
      goSignin() {
        this.$router.push({
          path: 'signin',
          name: 'signin'
        });
      }
    },
  };
</script>
